<template>
  <div id="group-discussion">
    <head-top :title="groupName">
      <router-link to="/teamRank">
        <i class="iconfont iconchengyuan"></i>
      </router-link>
    </head-top>

    <!-- 头部摘要 -->
    <div class="summary">
      <div>
        得分数:
        <span>{{score}}</span>
      </div>
      <div>
        论题:
        <span>{{total}}</span>
      </div>
      <div>
        点赞数:
        <span>{{good}}</span>
      </div>
    </div>

    <!-- 聊天信息 -->
    <div class="chat" ref="chat">
      <div v-for="(item, index) in dataList" :key="index">
        <div class="chat_title">
          <span class="chat_title_left">[论题]</span>
          <span v-if="item.Key['GSForm'] === contentTypeDefault ">{{ item.Key['GSContent'] }}</span>
          <img
            :src="getDiscussionImage( item.Key['GSContent'])"
            :style="{maxWidth:'70vw',maxHeight:'15rem'}"
            @click="preview(getDiscussionImage( item.Key['GSContent']))"
          />
        </div>

        <div v-show="item.Value && item.Value.length>0" class="chat_content">
          <div
            v-for="(sub, k) in item.Value"
            :key="k"
            :class=" sub['StudentID']!== $store.getters.studentID ? 'chat_item left_item' : 'chat_item right_item'"
          >
            <div class="chat_icon">
              <!-- {{sub['StudentPhoto']}} -->
              <img :src="defaultPhotoImg" />
            </div>

            <div class="chat_news">
              <div class="chat_news_name">{{sub['StudentName']}}</div>
              <div class="chat_news_cont">
                <span v-if="sub['GDForm'] === contentTypeDefault">{{sub['GDContent'] }}</span>
                <img
                  v-else
                  :src="getImageFile(sub['GDContent'])"
                  alt
                  @click="preview(getImageFile(sub['GDContent']))"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 输入框 -->
    <div class="footer" v-show="dataList.length>0">
      <div class="footer_text">
        <input
          type="text"
          v-model="content"
          :disabled="dataList.length===0"
          placeholder="请输入讨论内容"
          @keyup.enter="dataAdd"
        />
      </div>
      <div class="icons" v-if="content.length===0">
        <i class="iconfont icontupian" :disabled="dataList.length===0" @click="selectPhoto(1)"></i>
        <i class="iconfont iconphotograph" :disabled="dataList.length===0" @click="selectPhoto(2)"></i>
      </div>
      <div class="icons" v-else>
        <van-button size="small" type="primary" @click="dataAdd">发送</van-button>
      </div>
    </div>

  </div>
</template>

<script>
import headTop from "@/components/headTop";
import RImage from "@/components/RImage";
import { Button, Popup, ImagePreview } from "vant";
import defaultPhoto from "@/assets/boy_img.png";

import { ContentType, ModulePicture, DiscussFunc } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: { headTop, RImage, [Button.name]: Button, [Popup.name]: Popup },
  props: {},
  data() {
    return {
      score: 0,
      total: 0,
      good: 0,
      dataList: [],
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      content: "",
      imgBase: "",

      defaultPhotoImg: defaultPhoto,
      groupName: this.$store.getters.group
        ? this.$store.getters.group.groupName || "暂无组名"
        : "暂无组名",
      showEdit: false,
      imagePreview: null
    };
  },
  created() {
    this.getDataStatics();
    this.getDataDetail();
  },
  mounted() {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };
    });
  },
  methods: {
    preview(image) {
      this.imagePreview = ImagePreview({
        images: [image],
        showIndex: false,
        onClose() {}
      });
    },
    getDataStatics() {
      this.$gatewayHttp.groupDiscussion
        .getStatistic({
          groupID: this.$store.getters.groupID
        })
        .then(res => {
          if (res.Result) {
            this.score = res.Score || 0;
            this.total = res.Total || 0;
            this.good = res.Good || 0;
          }
        })
        .catch(err => {});
    },
    getDataDetail() {
      // this.dataList = [];
      this.$gatewayHttp.groupDiscussion
        .getList({
          groupID: this.$store.getters.groupID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              let index = this.dataList.findIndex(r => {
                return JSON.stringify(r.Key) === JSON.stringify(item.Key);
              });
              if (index === -1) {
                this.dataList.push(item);
                this.dataList.forEach(r => {
                  r.Value.sort(r => r["SubmitTime"]).reverse();
                });
              } else {
                item.Value.sort(r => r["SubmitTime"]).reverse();
                item.Value.forEach(sub => {
                  let subIndex = this.dataList[index].Value.findIndex(r => {
                    return JSON.stringify(r) === JSON.stringify(sub);
                  });
                  if (subIndex === -1) {
                    this.dataList[index].Value.push(sub);
                  }
                  this.$nextTick(() => {
                    this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
                    setTimeout(() => {
                      this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
                    }, 1000);
                  });
                });
              }
            });
          }
        })
        .catch(err => {});
    },
    dataAdd() {
      if (!this.content) {
        return;
      }
      this.$gatewayHttp.groupDiscussion
        .postDataSave({
          gsid: this.dataList[this.dataList.length - 1].Key["GSID"],
          studentID: this.$store.getters.studentID,
          groupID: this.$store.getters.groupID, // "19062313411770094101179090842305" ||
          gdForm: this.contentType,
          gdContent: this.content
        })
        .then(res => {
          if (res.Result) {
            // let subItem = {
            //   GDContent: this.content,
            //   GDForm: this.contentType,
            //   GDGood: false,
            //   GDID: "",
            //   GDScore: 0,
            //   GSID: this.dataList[this.dataList.length - 1].Key["GSID"],
            //   GatewayID: null,
            //   GroupID: null,
            //   IsPicContent: this.contentType !== this.contentTypeDefault,
            //   IsTextContent: this.contentType === this.contentTypeDefault,
            //   StudentID: this.$store.getters.studentID,
            //   StudentName: this.$store.getters.student["StudentName"],
            //   StudentPhoto: "",
            //   SubmitTime: ""
            // };
            // this.dataList[this.dataList.length - 1].Value.push(subItem);
            // setTimeout(() => {
            //   this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
            // }, 200);

            // this.getDataDetail();

            this.content = "";
            this.contentType = this.contentTypeDefault;
          }
        })
        .catch(err => {});
    },
    getDiscussionImage(fileId) {
      return this.$gatewayHttp.resourse.picture(
        fileId,
        ModulePicture.GroupSubject
      );
    },
    getImageFile(fileId) {
      return this.$gatewayHttp.resourse.picture(
        fileId,
        ModulePicture.GroupDiscuss
      );
    },
    selectPhoto(type) {
      if (this.dataList.length === 0) return;
      if (type === 2) {
        sendToAPP(FuncType.cameraOpen, "", 1, 1);
      } else {
        sendToAPP(FuncType.albumOpen, "", 1, 1);
      }
    },
    afterRead(file) {
      console.log("接收到的base64：", file);
      // if (
      //   file.type !== "image/jpeg" &&
      //   file.type !== "image/png" &&
      //   file.type !== "image/jpg"
      // ) {
      //   this.$toast("请上传 jpg 格式图片");
      //   return false;
      // }
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: ModulePicture.GroupDiscuss,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            if (res.Result) {
              this.contentType = ContentType.Picture;
              this.imgBase = file;
              this.content = res.Data;
              this.dataAdd();
            }
          })
          .catch(err => {});
      }
    }
  },
  computed: {},
  watch: {
    "$store.getters.groupDiscussion"(newVal, oldVal) {
      if (newVal !== oldVal) {
        if (newVal.type === DiscussFunc.init) {
          this.getDataStatics();
          this.getDataDetail();
          this.dataList = [];
        } else if (newVal.type === DiscussFunc.newMsg) {
          this.getDataDetail();
          // for (let i = 0; i < this.dataList.length; i++) {
          //   if (this.dataList[i].Key["GSID"] === newVal.value["GSID"]) {
          //     if (this.dataList[i].value.indexOf(newVal.value) === -1) {
          //       this.dataList[i].value.push(newVal.value);
          //     }
          //     break;
          //   }
          // }
        } else if (newVal.type === DiscussFunc.good) {
          this.good += newVal.value["IsGood"] ? 1 : -1;
        } else if (newVal.type === DiscussFunc.score) {
          this.score += newVal.value["IsAdd"] ? 1 : -1;
        }
      }
    },
    dataList(newVal, oldVal) {
      // setTimeout(() => {
      this.$nextTick(() => {
        this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
        setTimeout(() => {
          this.$refs["chat"].scrollTop = this.$refs["chat"].scrollHeight;
        }, 1000);
      });
      // }, 200);
    }
  },
  beforeDestroy() {
    if (this.imagePreview) this.imagePreview.close();
  }
};
</script>

<style lang='less' scoped>
#group-discussion {
  width: 100%;
  height: 100%;
  background: #ebf0ef;

  .iconchengyuan {
    font-size: 1.7rem;
    position: absolute;
    top: 1.3rem;
    right: 2rem;
    color: #fff;
  }

  .summary {
    width: 93vw;
    height: 4.5rem;
    padding: 0 3.5vw;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: 1.4rem;
    font-family: MicrosoftYaHei;
    color: rgba(153, 153, 153, 1);
    line-height: 4.5rem;

    span {
      font-size: 2rem;
      font-family: DINPro-Regular;
      color: rgba(240, 122, 35, 1);
    }
  }

  .chat {
    padding: 0 3vw;
    text-align: left;
    height: calc(100vh - 4.3rem - 4.5rem - 6rem);
    overflow-y: auto;

    .chat_title {
      display: flex;
      align-items: flex-start;
      margin: 0.5rem 0;

      font-size: 1.5rem;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);

      .chat_title_left {
        min-width: 5rem;
      }
      .chat_img {
        padding-top: 0.3rem;
        /deep/img {
          object-position: left;
        }
      }
    }

    .chat_content {
      .chat_item {
        width: 100%;
        margin-bottom: 1.6rem;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        .chat_icon {
          margin-right: 0.5rem;
          img {
            margin-top: 0.2rem;
            max-width: 11vw;
            max-height: 5rem;
            border-radius: 50%;
          }
        }
        .chat_news {
          max-width: 80vw;

          .chat_news_name {
            font-size: 1.2rem;
            line-height: 2;
            color: rgba(102, 102, 102, 1);
          }
          .chat_news_cont {
            background: #fff;
            border-radius: 0.5rem;
            padding: 0.5rem 1rem;
            font-size: 1.4rem;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }
        }
      }
      .right_item {
        flex-direction: row-reverse;
        .chat_icon {
          margin-left: 0.5rem;
        }
        .chat_news {
          .chat_news_name {
            text-align: right;
          }
          .chat_news_cont {
            background: rgba(0, 203, 171, 1);
            color: #fff;
            text-align: right;
          }
        }
      }
    }
  }

  @footHeight: 6rem;
  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: @footHeight;
    background: rgba(255, 255, 255, 1);
    overflow: hidden;
    text-align: left;
    .footer_text {
      width: 75vw;
      height: @footHeight;
      line-height: @footHeight;
      margin: 0 2.5vw;

      float: left;
      text-align: left;
      border: none;

      input {
        background: rgba(235, 240, 239, 1);
        font-size: 1.5rem;
        color: rgba(153, 153, 153, 1);
        width: 100%;
        height: @footHeight - 2rem;
        padding-left: 2vw;
      }
    }
    .icons {
      width: 18vw;
      height: @footHeight;
      line-height: @footHeight;
      float: left;
      padding-left: 2vw;
      .iconfont {
        font-size: 2.4rem;
      }
      .van-button {
        line-height: @footHeight - 2.5rem;
        height: @footHeight - 2.5rem;
        font-size: 1.3rem;
        padding: 0 2vw;
      }
    }
  }

  // .pop_dialog {
  //   width: 70vw;
  //   // height: 20vh;
  //   font-size: 1.5rem;
  //   line-height: 2;
  //   .pop_head {
  //     margin: 1.5vh 0;
  //   }
  //   input {
  //     background-color: #f8f8f8;
  //     border: 1px solid #f2f2f2;
  //     color: #323233;
  //     // padding: 0 1vw;
  //     width: 90%;
  //   }
  //   button {
  //     margin: 1.7vh 0;
  //     width: 90%;
  //     color: #fff;
  //     background-color: #00cbab;
  //   }
  // }
}
</style>


